<template>
  <div id="app">
    <label for="search">
      <div class="input-group mb-3">
        <input type="text" id="search" class="form-control" v-model="searchKeyWords" placeholder="">
        <button class="btn btn-outline-success searchBtn" @click="searchKeyword"></button>
      </div>
    </label>
    <div class="table-responsive">
      <table class="table table-striped table-bordered">
        <thead class="thead-dark">
          <tr>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody v-for="patient in patients" :key="patient.patient_id">
          <tr>
            <td>{{ patient.name }}</td>
            <td>{{ genderText[patient.gender] }}</td>
            <td>{{ patient.ID_Number}}</td>
            <td>{{ patient.email }}</td>
            <td>{{ patient.phone}}</td>
            <td>
              <button class="btn btn-outline-secondary" @click="showDetails(patient.patient_id)"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
  <PatientInfoDetailsModal :patientinfo-detail="healthConditions"></PatientInfoDetailsModal>
</template>

<script>
/* eslint object-curly-newline: "off" */
import * as bootstrap from 'bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import { checkSuccessMsg, checkErrorMsg, searchNoKeyword, searchDataNum } from '../toastMessage';
import PatientInfoDetailsModal from '../../components/PatientInfoDetailsModal.vue';

export default {
  data() {
    return {
      fixApi: 'https://140.123.173.4',
      api: '',
      searchKeyWords: '',
      patients: [],
      // filtered_UserInfo: [],
      patientInfoDetails_modal: {},
      healthConditions: {
        modalTitle: '',
        modalContent: '',
      },
    };
  },
  computed: {
    //  filterUserInfo 
    // filteredUserInfo() {
    //   return this.filtered_UserInfo;
    // },
    genderText() {
      return {
        0: '',
        1: '',
      };
    },
  },
  methods: {
    // filterUserInfo() {
    //   if (!this.searchKeyWords) {
    //     this.filtered_UserInfo = this.userInfo;
    //   } else {
    //     this.filtered_UserInfo = this.userInfo.filter((user) => Object.values(user).some((val) => String(val).toLowerCase().includes(this.searchKeyWords.toLowerCase())));
    //   }
    // },
    // 
    checkedLoginStatus() {
      // eslint-disable-next-line
      const token = document.cookie.replace(/(?:(?:^|.*;\s*)hasToken\s*\=\s*([^;]*).*$)|^.*$/, '$1'); //  hasToken  cookie
      this.$http.defaults.headers.common.Authorization = token; //  Token  Headers Authorization 
      // console.log(token);
      const api = `${this.fixApi}/nutritionist/loginCheck`;
      //  /user/check API
      this.$http.get(api).then((status) => {
        // console.log(status);
        if (status.data.success) {
          checkSuccessMsg();
        }
      }).catch(() => {
        checkErrorMsg();
      });
    },
    searchKeyword() {
      const api = `${this.fixApi}/patient/`;
      const keyword = this.searchKeyWords.trim();

      this.$http.get(api, { params: { keyword } }).then((res) => {
        console.log(res.data);
        this.patients = res.data[0].result;
        if (keyword === '') {
          searchNoKeyword();
        } else {
          searchDataNum(res.data[0].total);
        }
        // this.filterUserInfo();
        this.searchKeyWords = '';
      }).catch((err) => {
        console.log(err);
      });
    },
    search() {
      const api = `${this.fixApi}/patient/`;

      this.$http.get(api).then((res) => {
        console.log(res.data);
        this.patients = res.data[0].result;
        this.searchKeyWords = '';
      }).catch((err) => {
        console.log(err);
      });
    },
    showDetails(patientID) {
      const api = `${this.fixApi}/patient/healthConditions/${patientID}`;
      this.$http.get(api).then((res) => {
        this.healthConditions = res.data;

        // 
        this.healthConditions.modalTitle = `${this.healthConditions.name} `;
        this.healthConditions.modalContent = `${this.healthConditions.doctorOrder}<br>`
          + `${this.healthConditions.activeStatus}<br>`
          + `${this.healthConditions.sportsStatus}<br>`
          + `${this.healthConditions.symptom}<br>`
          + `${this.healthConditions.disease}<br>`
          + `${this.healthConditions.knowledge}<br>`
          + `${this.healthConditions.compliance}<br>`
          + `${this.healthConditions.height}<br>`
          + `${this.healthConditions.weight}<br>`
          + `BMI${this.healthConditions.bmi}<br>`
          + `${this.healthConditions.weight_change}<br>`
          + `/${this.healthConditions.caloriesEaten} <br>`
          + `/${this.healthConditions.caloriesDemand} <br>`;
        console.log(this.healthConditions.modalContent);
        //  Modal 
        this.patientInfoDetails_modal.show();
      }).catch((err) => {
        console.log(err);
        this.healthConditions.modalTitle = '';
        this.healthConditions.modalContent = '';
        this.patientInfoDetails_modal.show();
      });
    },
  },
  components: {
    PatientInfoDetailsModal,
  },
  created() {
    this.search();
  },
  mounted() {
    this.checkedLoginStatus();

    const modal = new bootstrap.Modal(document.getElementById('patientInfoDetailsModal'));
    this.patientInfoDetails_modal = modal;
  },
};
</script>
<style lang="scss">
</style>
